import { Formik,Field, Form } from "formik";
import React from "react";
import DialogItem from "./DialogItem/DialogItem"
import s from "./Dialogs.module.css"
import Message from "./Message/Message";




const Dialogs = (props) => {
  let dialogsElements = props.dialogsPage.dialogs.map(d => <DialogItem key={d.id} name={d.name} id={d.id} />);
  let messagesElements = props.dialogsPage.messages.map(m => <Message message={m.message} key={m.id} direction={m.direction} />);


  let sendMsg = (obj) => {
    props.sendMsg(obj.bodyField);
  }



  const NewMessageForm = (props) => {

    return (
      <Formik onSubmit={props.onSubmit} initialValues={{bodyField:''}}>
        <Form>
        <div className={s.newMessage} >
          <Field name="bodyField" component="textarea" placeholder="Текст ответа" /><br />
          <button>Отправить</button>
        </div>
        </Form>
      </Formik>
    );

  }

  
  return (
    <div className={s.dialogs}>
      <div className={s.dialogsItems}>
        {dialogsElements}
      </div>
      <div>
        <div className={s.messages}>
          {messagesElements}
        </div>

        <NewMessageForm  onSubmit={sendMsg}/>

      </div>
    </div>
  )
}


export default Dialogs